import { IActionOrTriggerListPageParams, routerUtils } from "@app/static";
import { androidDispatchers, autoWebViewJs, isRealInAndroid, openHtmlShowA11yNodeInfoButton, windowUtils } from "@droid/android";
import { actionA11yNodeExecuteUtils, getTriggerOrActionListPageParams, IA11yNodeInfo, IOnChangeActionJavaDataProps, projectEventUtils, ProjectMemberType } from "@droid/project-interface";

import { createActionOrTriggerButtonRender, IActionOrTriggerItem } from "@src/components/trigger-list-action-list-common/ITriggerList-IActionList-common.intf";

import { NextRouter } from "next/router";
import { ulid } from "ulid";
import { getRandomMockA11yNodeInfo } from "./mockA11yNodeInfo";
import { getProxyDispatcher, toProxy } from "../../../../../../../../../libs/fanfanlo";

export const a11yNodeSelectActionItem:IActionOrTriggerItem = {
    id: "a11y-node-select-action-item-df1c",
    i18nNs: "homepage/components/page/action-list/groups/a11y/node-select/content",
    nameKey: "content.name",
    group: "a11yNodeSelectActionItem.group",
    descriptionKey: "content.description",
    type: "action",
    LinkRender: createActionOrTriggerButtonRender(onAddSelect)
}
actionA11yNodeExecuteUtils.onClickChange = onChangeSelect

async function onSelect():Promise<IA11yNodeInfo>{
    return new Promise((resolve)=>{

        if(!isRealInAndroid){
            return resolve(getRandomMockA11yNodeInfo())
        }
        const eventType = "a11yNodeSelectActionItem"
        const listenIndex = autoWebViewJs.shellListen(androidDispatchers.WebViewJsDispatcher.name, eventType, eventType, false, onSelectNode)
        function onSelectNode(s:string) {
            const info:IA11yNodeInfo = JSON.parse(s)
            console.log('fffffffffffffff', s)
            windowUtils.exitWindowById(windowId)
            autoWebViewJs.shellRemoveListen(androidDispatchers.WebViewJsDispatcher.name, listenIndex);
            resolve(info)
        }
        const windowId = ulid()
        openHtmlShowA11yNodeInfoButton(eventType, windowId)
    })
}
async function onAddSelect({query, router}:{query:IActionOrTriggerListPageParams, router:NextRouter}){
    const nowPath = router.pathname
    const info = await onSelect()
    const action = actionA11yNodeExecuteUtils.mergeDataToDefultData({})
    action.javaData.info = info
    const res = getTriggerOrActionListPageParams(query)
    projectEventUtils.dispatchEditEvent(projectEventUtils.EditType.add,{
        data:action,
        projectMemberType:ProjectMemberType.actionGroup,
        projectInfo:res.data
    })
    routerUtils.checkToPath(router, nowPath, query.backPath || "", 1)
    autoWebViewJs.openSelfByPackage()
}

async function onChangeSelect({query,onDataSelectedEventType}:IOnChangeActionJavaDataProps){
    console.log('lllllllllllllllllllllllllll2223333')
    const info = await onSelect()
    // onJavaDataChangeDataSelect?.(info)
    const res = getTriggerOrActionListPageParams(query)
    if(onDataSelectedEventType && res.data.action){
        console.log('fffffffffffrom event type ')
        getProxyDispatcher(res.data.action).dispatch(onDataSelectedEventType, info)
        return
    }
    projectEventUtils.dispatchEditEvent(projectEventUtils.EditType.updateJavaData,{
        data:{info},
        projectMemberType:ProjectMemberType.action,
        projectInfo:res.data
    })
    autoWebViewJs.openSelfByPackage()
}