import { IActionOrTriggerListPageParams } from "@app/static";
import { autoWebViewJs, isRealInAndroid, openPnAnInfoWindow, PnAnResultData } from "@droid/android";
import { actionToAppMainPageUtils, getTriggerOrActionListPageParams, projectEventUtils, ProjectMemberType } from "@droid/project-interface";
import { createRandomEnglishCharacters } from "@fanfanlo";
import { createActionOrTriggerButtonRender, IActionOrTriggerItem } from "@src/components/trigger-list-action-list-common/ITriggerList-IActionList-common.intf";


export const toAppMainPageActionItem:IActionOrTriggerItem = {
    id: "to-app-main-page-action-item-df1c",
    i18nNs: "homepage/components/page/action-list/groups/app/to-app-main-page/content",
    nameKey: "content.name",
    group: "toAppMainPageActionItem.group",
    descriptionKey: "content.description",
    type: "action",
    LinkRender: createActionOrTriggerButtonRender(onAddSelect)
}
actionToAppMainPageUtils.onClickChange = onChangeSelect


async function onSelect(){
    let info:PnAnResultData ;
    if(isRealInAndroid){
        info = await openPnAnInfoWindow(true)
    }else{
        info = {
            pn:`${createRandomEnglishCharacters(Math.floor(Math.random() * 5) + 5)}.${createRandomEnglishCharacters(Math.floor(Math.random() * 5) + 5)}.${createRandomEnglishCharacters(Math.floor(Math.random() * 5) + 5)}`,
            an:createRandomEnglishCharacters(Math.floor(Math.random() * 5) + 5)
        }
    }
    // console.log('info1=', info)
    return info
}
async function onAddSelect({query}:{query:IActionOrTriggerListPageParams}){
    const info = await onSelect()
    const res = getTriggerOrActionListPageParams(query)
    const action = actionToAppMainPageUtils.mergeDataToDefultData({javaData:{pn:info.pn, an:info.an}})
    projectEventUtils.dispatchEditEvent(projectEventUtils.EditType.add,{
        data:action,
        projectMemberType:ProjectMemberType.actionGroup,
        projectInfo:res.data
    })
    window.history.back()
    autoWebViewJs.openSelfByPackage()
}

async function onChangeSelect({query}:{query:IActionOrTriggerListPageParams}){
    const info = await onSelect()
    const res = getTriggerOrActionListPageParams(query)
    projectEventUtils.dispatchEditEvent(projectEventUtils.EditType.updateJavaData,{
        data:info,
        projectMemberType:ProjectMemberType.action,
        projectInfo:res.data
    })
    autoWebViewJs.openSelfByPackage()
}