import { IActionOrTriggerListPageParams } from "@app/static"
import { Button } from "@mui/material"
import Link from "next/link"
import { NextRouter, useRouter } from "next/router"
import { ParsedUrlQueryInput } from "querystring"
import { PropsWithChildren } from "react"
export type IActionItemType = "page" | "action" | "trigger"
export interface IActionOrTriggerItem {
    id:string
    i18nNs:string
    nameKey: string
    group: string
    descriptionKey: string
    type: IActionItemType
    LinkRender:React.ComponentType<{query:IActionOrTriggerListPageParams, id:string, item:IActionOrTriggerItem}&PropsWithChildren>
}

export type IActionOrTriggerList = IActionOrTriggerItem[]

export type IActionOrTriggerGroup = {
    i18nNs:string
    nameKey: string
    descriptionKey: string
    items: IActionOrTriggerList
}

export type IActionOrTriggerGroupList = IActionOrTriggerGroup[]

export function ActionOrTriggerListLinkRender({query, children, pathname, id}:{query:IActionOrTriggerListPageParams, pathname:string, id:string}&PropsWithChildren) {
    return (
        <Link href={{pathname, query}} data-id={id}
        >
            {children}
        </Link>
    )
}

export function createActionOrTriggerListLinkRender(subPath:string, id:string, extraQuery?:ParsedUrlQueryInput) {
    return ({query, children, item}:{query:IActionOrTriggerListPageParams, item:IActionOrTriggerItem}&PropsWithChildren) => {
        const router = useRouter()
        const pathname = `${router.pathname}${subPath}`
        return <ActionOrTriggerListLinkRender query={{...query, ...extraQuery}} pathname={pathname} id={id}>{children}</ActionOrTriggerListLinkRender>
    }
}


export function createActionOrTriggerButtonRender(onSelect:(params:{query:IActionOrTriggerListPageParams, item:IActionOrTriggerItem, router:NextRouter})=>void){
    return ({query, children, item}:{query:IActionOrTriggerListPageParams, item:IActionOrTriggerItem}&PropsWithChildren) => {
        const router = useRouter()
        function onClick() {
            onSelect({query, item, router})  
        }
        return(
            <Button id={item.id} 
            data-id={item.id}
            onClick={onClick}>
                {children}
            </Button>
        )
    }
}