import { autoWebViewJs } from "../../AutoWebViewJs"
async function exitWindowById(id:string){
    const script = `var bl = com.fanfanlo.lib.components.floatview.floatwindow.WindowUtils.exitById("${id}", "");\n bl;`
    console.log('exitWindowById_fn called', id, script)
    return autoWebViewJs.callScript<boolean>(script)
}
export const windowUtils = {
    exitWindowById
}